@font-face {
  font-family: 'RobotoMono';
  src: url('./fonts/RobotoMono-Thin.ttf')
}

* {
  font-family: 'RobotoMono', monospace;
}

html, body {
  margin: 0;
  overflow-y: hidden;
}

body {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  text-align: right;
  line-height: 220%;
  color: #fff;
  background-color: rgb(50, 56, 73);
}